<template>
  <el-container style="height:100vh;">
    <el-header class="head">
      <!-- <el-image style="height:61px" :src="require('../../assets/logo.png')" fit="cover"></el-image> -->
    </el-header>
    <el-container>
      <el-aside width="200px">
        <div class="infoC">
          <el-image style="width: 50px; height: 50px" :src="url"></el-image>
          <div class="info">
            <label>张医生</label>
            <label style="font-size:14px;">呼吸内科一诊室</label>
          </div>
        </div>
        <router-link v-bind:to="routUrl">
          <div class="manageBtn">
            <i class="el-icon-monitor"></i>
            <label style="margin-left:10px">转诊管理</label>
          </div>
        </router-link>
      </el-aside>
      <el-main style="background: #F2F6FF;">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      logoUrl: "../../assets/logo.png",
      routUrl: "/home/manage",
      date: "",
      url:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
    };
  },
  computed: {}
};
</script>
<style scoped>
.head{
background: url(../../assets/logo.png) no-repeat;
height: 60px;
    background-size: auto 44px;
    background-position: 16px 8px;
}
.el-aside {
  background: #488aff;
}
.infoC {
  margin: 20px 0px 0px 15px;
  display: flex;
}
.info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 5px;
  font-size: 16px;
  color: white;
}
.manageBtn {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  margin-top: 10px;
  font-size: 16px;
  color: white;
  background: #2d71e7;
}
.el-main {
  padding: 0px;
}
</style>



